@-webkit-keyframes floating {
    0% { -webkit-transform: translate(0px, -0px); transform: translate(0px, -0px); }
    45% { -webkit-transform: translate(2px, 4px); transform: translate(2px, 4px); }
    65% { -webkit-transform: translate(3px, 2px); transform: translate(3px, 2px); }
    85% { -webkit-transform: translate(-3px, 6px); transform: translate(-3px, 6px); }
    100% { -webkit-transform: translate(0px, -0px); transform: translate(0px, -0px); } 
}

    @keyframes floating {
    0% { -webkit-transform: translate(0px, -0px); transform: translate(0px, -0px); }
    45% { -webkit-transform: translate(2px, 4px); transform: translate(2px, 4px); }
    65% { -webkit-transform: translate(3px, 2px); transform: translate(3px, 2px); }
    85% { -webkit-transform: translate(-3px, 6px); transform: translate(-3px, 6px); }
    100% { -webkit-transform: translate(0px, -0px); transform: translate(0px, -0px); } 
}

    @-webkit-keyframes materialise {
    0% { opacity: 100%; }
    50% { opacity: 0.1; }
    100% { opacity: 100%; }
    }

    @keyframes materialise {
    0% { opacity: 100%; }
    50% { opacity: 0.1; }
    100% { opacity: 100%; }
    }

